import React from 'react';
import { Layout, Tabs } from "antd";
import HeadBarView from "../../components/HeadBar/index";
import ContentView from "./Content/ContentView";
import PannelView from "./PannelView/index";
import ListView from './ListView/ListView'
import SearchView from './SearchView/SearchView';
import "./Homepage.less";

const { Header,  Content } = Layout;
const { TabPane } = Tabs;


export default class Homepage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            openSearchView: false,
            pageList: []
        }
        this.per_page = 20
    }

    componentWillMount() {
        // this.queryPubilshedPages()
      }
    
    queryPubilshedPages= () => {
        let params = {
            per_page: this.per_page,
            page: 1,
        }
        api.getReport(params).then((response) => {
            const data = response.data.data
            this.setState({
                page: 1,
                totalCount: data.pageInfo.total,
                totalPage: data.pageInfo.pages,
                isShowLoading: false,
                pageList: data.reportList
            })
        })
    }
    
    openSearch = () => {
        console.log('open')
        this.setState({
            openSearchView: true
        })
    }

    closeSearch = () => {
        console.log('close')
        this.setState({
            openSearchView: false
        })
    }

    render() {
        const list = () => {
            return <ListView {...this.props}></ListView>
        }
        const pannel = () => {
            return <PannelView {...this.props}></PannelView>
        }
        // const items = [{ label: '数据快报', key: '1', children: list() }, { label: '数据上传', key: '2', children: pannel() }]
        // const items = [{label:'数据快报',key:'1',children:list()}]
        
        return (
            <Layout style={{position: 'relative', minHeight:document.documentElement.clientHeight}}>
                <Header className='home-header' style={{ height: "50px" }}>
                    <HeadBarView {...this.props} />
                </Header>
                <Content className="pageWrapper">
                    <ContentView {...this.props}>
                        <Tabs defaultActiveKey='1' >
                            <TabPane key='1' tab="数据快报">
                                {/* {this.state.openSearchView
                                    ? <SearchView closeSearch={this.closeSearch}></SearchView>
                                    : <ListView openSearch={this.openSearch} {...this.props}></ListView>} */}
                                <ListView openSearch={this.openSearch} {...this.props}></ListView>
                            </TabPane>
                            <TabPane key='2' tab="数据上传">
                                <PannelView {...this.props}></PannelView>
                            </TabPane>
                        </Tabs>
                    </ContentView>
                </Content>

                <div className='bottomTip'>
                    <div className='logo'></div>
                    <span>Powered By Calliope</span>
                </div>
            </Layout >
        )
    }
}